.transactionHistory {
  font-size: 18px;
  text-align: center;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;

  width: 800px;
  background: #fcfcfc;
  border-radius: 0px 0px 2px 2px;
}

thead {
  height: 40px;
  background-color: rgb(42, 188, 207);
  text-transform: uppercase;
  color: #fff;
  font-weight: 400;
  letter-spacing: 2px;
}
tbody tr {
  height: 40px;
}
